img {
    position: relative;
    bottom:0;
    top:0;
    float:left;
    width: 35%;
    height: 100%;
    margin-right: 1em;
    box-sizing: border-box;
    border-radius: 10%;
  }

  .round-image {
    border-radius: 25px;
    border: none;
  }
  
  .recipe-card {
    border: 2px solid darkolivegreen;
    text-transform: capitalize;
    width: 60%;
    height:100%;
    border-radius: 10px;
    float: right;
    box-sizing: border-box;
    padding: 1em;
    margin: 1em; 
    line-height: 1.5em;
    color: darkslateblue;
    position: relative;
    background-color: beige;
  }

  .recipe-card:nth-of-type(odd){
    background-color: whitesmoke;
    float: left;
    color: black;
    border: 5px solid silver;  
}

a {
    font-size: 0.7em;
    margin-left: 0.7em;
    background-color: lightgreen;
    padding: 1.2em;
    border-radius: 20px;
    color: darkgreen;
}

.recipe-card:nth-of-type(odd) a{
 
  font-size: 0.7em;
  margin-left: 0.7em;
  background-color: purple;
  padding: 1.2em;
  border-radius: 20px;
  color: whitesmoke;
}

.recipe-label{

    flex: 1;
    word-break: break-word;
    margin: .5rem;
    line-height: 1rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: 600;
    line-height: 1;
    letter-spacing: 4px;
    z-index: 2;
    position: relative;
    transition: all .75s;

}

.recipe-info {
  margin-left: 10px;
}